export const timeFormatter = function (_time) {
  var seconds = Math.floor((new Date() - new Date(_time)) / 1000);

  var interval = seconds / 31536000;

  if (interval > 1) {
    return Math.floor(interval) + " nm trc";
  }
  interval = seconds / 2592000;
  if (interval > 1) {
    return Math.floor(interval) + " thng trc";
  }
  interval = seconds / 86400;
  if (interval > 1) {
    return Math.floor(interval) + " ngy trc";
  }
  interval = seconds / 3600;
  if (interval > 1) {
    return Math.floor(interval) + " gi trc";
  }
  interval = seconds / 60;
  if (interval > 1) {
    return Math.floor(interval) + " pht trc";
  }
  return Math.floor(seconds) + " giy trc";
};
